import React from 'react';



function FollowUs() {
  return (
    <div className="follow-container display-flex container pt-5 pb-5 mt-5 ">
      <div className="text-container ms-5 ps-5">
        <strong className='display-2'>Follow us </strong>
        <p className="display-6">Get regular updates by following us</p>
      </div>
      <div className="icon-container display-4 d-flex ms-5 ps-5 ">
        <a href="https://twitter.com/" target="_blank" rel="noopener noreferrer">
        <i class="bi bi-twitter"></i>
        </a>
        <a href="https://instagram.com/" target="_blank" rel="noopener noreferrer">
        <i class="bi bi-instagram"></i>
        </a>
        <a href="https://linkedin.com/" target="_blank" rel="noopener noreferrer">
        <i class="bi bi-linkedin"></i>
        </a>
        <a href="https://plus.google.com/" target="_blank" rel="noopener noreferrer">
        <i class="bi bi-youtube"></i>
        </a>
      </div>
    </div>
  );
}

export default FollowUs;