import React from 'react';
import Data from '../Components/Data';
import Three from './Three';

const ncard=(value)=>{
    return(
    <Three id={value.id} 
        name={value.name}
        designation={value.designation}
        contact={value.contact}
    />)
}

const Card=()=>{
    return(
        <>
        <div className='  fixed-top' id='0'></div>
        <h1 className=' display-4 text-center py-5'>Our Employees</h1>
        <div  className=' d-grid place-items-center container'>
        <table>
        <tr className="text-center">
            <th>Sr.no</th>
            <th>Name</th>
            <th>Designation</th>
            <th>Contact</th>
        </tr>
        
            {Data.map(ncard)}
</table>
            </div>
        </>
    )
}

export default Card;
